import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Form, Col, Modal, Table, message } from 'antd';
import { TableData, SelectAjax, YearSemesterInput } from 'components/common/index';
import { getQueryParams } from 'utils/urlUtil';
import {
  getPolicyStudents,
  createPolicyStudents,
  deletePolicyStudents,
} from 'services/policyStudent';
import { getPolicyObjects } from 'services/policyObjectForPolicyStudent';
import { dateToString } from 'utils/formatUtil';
import { BtnSave, BtnCancel } from 'components/common/button/index';
import SearchBar from './SearchBar';
import './style.scss';

@Form.create({ name: 'formListPolicyObject' })
class ListPolicyObject extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    visible: false,
    type: '',
    enableButton: '',
    dataSearchTable: [],
    dataForSetPolicyStudents: [],
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  showModal = type => {
    this.setState({
      visible: true,
      type,
    });
    const { paramSearch } = this.state;
    console.log(paramSearch.semesterId);
    console.log(paramSearch.yearEduId);
    const { form } = this.props;
    form.setFieldsValue({ semesterId: paramSearch.semesterId });
    form.setFieldsValue({ yearEduId: paramSearch.yearEduId });
  };

  hideModal = type => {
    this.setState({
      visible: false,
      type,
    });
    const { dataSearchTable } = this.state;
    console.table(dataSearchTable);
    const data = dataSearchTable.map(x => {
      const { studentId } = x;
      const { semesterId } = x;
      const { yearEduId } = x;
      const { policyObjectId } = x;
      return { studentId, semesterId, yearEduId, policyObjectId };
    });
    this.cancelDataPolicyStudents(data);
    this.setState({ reload: this.state.reload + 1 });
    message.success('Xa thnh cng');
  };

  cancelDataPolicyStudents = async data => {
    try {
      console.table(data);
      await deletePolicyStudents(data);
      // this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  handleOk = e => {
    e.preventDefault();
    this.setState({
      visible: false,
    });

    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const { dataForSetPolicyStudents } = this.state;
      const data = dataForSetPolicyStudents.map(x => {
        const { studentId } = x;
        return { studentId, ...values };
      });
      this.saveDataPolicyStudents(data);
    });
  };

  saveDataPolicyStudents = async data => {
    try {
      console.table(data);
      await createPolicyStudents(data);
      this.props.history.goBack();
      this.setState({ reload: this.state.reload + 1 });
      message.success('Thm thnh cng');
    } catch (error) {
      console.log(error);
    }
  };

  handleCancel = e => {
    e.preventDefault();
    this.setState({
      visible: false,
    });
  };

  render() {
    this.columns = [
      {
        title: 'M SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
      },
      {
        title: 'Lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Tn i tng ',
        dataIndex: 'policyObjectLabel',
        key: 'policyObjectLabel',
      },
    ];

    const rowSelectedOfDataSearchTable = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ dataSearchTable: selectedRows });
      },
    };

    const rowSelectedSetPolicyStudents = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ dataForSetPolicyStudents: selectedRows });

        if (this.state.dataSearchTable.policyObjectId == null) {
          this.setState({ enableButton: 'disabled' });
        }
      },
    };

    const { getFieldDecorator } = this.props.form;
    const { paramSearch = {} } = this.state;
    return (
      <Fragment>
        <Card title="Chn i tng chnh sch">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableData
                rowSelection={rowSelectedOfDataSearchTable}
                reload={this.state.reload}
                paramSearch={this.state.paramSearch}
                allowGetData={
                  !!paramSearch.trainTypeId &&
                  !!paramSearch.facultyId &&
                  !!paramSearch.courseId &&
                  !!paramSearch.semesterId &&
                  !!paramSearch.yearEduId
                }
                service={getPolicyStudents}
                columns={this.columns}
                rowKey="id"
              />
            </Col>
            <Col span={24} style={{ textAlign: 'center' }}>
              <div className="btn-group">
                <BtnSave onClick={() => this.showModal('set')} title="Xt i tng" />
                <BtnCancel
                  onClick={() => this.hideModal('unset')}
                  title="Hy i tng"
                  disabled={this.state.enableButton}
                />
              </div>
            </Col>
          </Row>
          <Modal
            style={{ top: 20 }}
            width="80%"
            title={this.state.type === 'set' ? 'Xt i tng' : 'Hy i tng'}
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            okText="ng "
            cancelText="Hy"
          >
            <Row>
              <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
                <Col span={24}>
                  <YearSemesterInput form={this.props.form} />
                  <Form.Item label="TCS">
                    {getFieldDecorator(`policyObjectId`, {
                      // initialValue: initialValue.noOfStudent,
                      rules: [
                        {
                          required: true,
                          message: 'Vui lng nhp thng tin!',
                        },
                      ],
                    })(<SelectAjax service={getPolicyObjects} />)}
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Table
                    rowSelection={rowSelectedSetPolicyStudents}
                    dataSource={this.state.dataSearchTable}
                    columns={this.columns}
                    rowKey="id"
                  />
                </Col>
              </Form>
            </Row>
          </Modal>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPolicyObject />
  </Page>
);



